@import "../Common/Styles/SASS/Variables";


.mainBlock {
  height: 100vh;
  z-index: 1;
  background-position: center;
  background-size: 40% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-image: url("../assets/image/photo_6.jpg");
  background-color: rgba(30, 30, 30, 1);

  > div {
    width: 100%;
  }

  @media (width: 1024px) {
    background-size: 56vh 100%;
  }
  @media (max-width: 990px) {
    background-size: 52vh 100%;
    background-position: center center;
    background-attachment: fixed;
  }
  @media (max-width: 414px) {
    background-size: 50vh 100%;
  }


  .text {
    padding-top: 300px;
    width: 370px;
    color: $textColor_h2;

    @media (width: 1024px) {
      padding-top: 600px;
    }
    @media (max-width: 320px) {
      padding-top: 200px;
    }
    @media (max-width: 280px) {
      padding-top: 300px;
    }

    .mainName {
      font-family: 'Poppins', sans-serif;
      font-size: 4em;
      letter-spacing: 3px;
      position: absolute;
      top: 50%;
      left: 50%;
      padding: 10px;
      transform: translate(-50%, -20%);
      text-shadow: 8px 0 1px rgba(255, 255, 255, 0.1);
      -webkit-transform: translate(-50%, -20%);

      @media (max-width: 1240px) {
        font-size: 7vh;
      }
      @media (max-width: 990px) {
        font-size: 6vh;
      }
      @media (max-width: 450px) {
        font-size: 4vh;
      }
    }

    .textRun {
      font-size: 2em;
      padding: 10px 0;
      color: $textColor_h2;
      margin-top: 120px;
      margin-bottom: 1rem;

      @media (width: 1024px) {
        margin-top: 260px;
        margin-bottom: 2rem;
      }
      @media (height: 768px) and (width: 1024px) {
        margin-top: -60px;
      }
      @media (width: 768px) {
        margin-top: 319px;
        margin-bottom: 2rem;
      }
      @media (width: 720px) {
        margin-top: 40px;
      }
      @media (max-width: 680px) {
        font-size: 3.5vh;
      }

      .primaryColor {
        color: $textColor_Primary;
      }
    }


    p {
      white-space: nowrap;
      line-height: 50px;
      overflow: hidden;
      animation: step 3s steps(50) infinite;
    }

    /*   Анимация   */
    @keyframes step {
      0% {
        width: 0;
      }
      100% {
        width: 100%;
      }
    }
    @-webkit-keyframes step {
      0% {
        width: 0;
      }
      100% {
        width: 100%;
      }
    }

    .containerContactMe {
      padding-top: 30px;

      @media (max-width: 320px) {
        padding-top: 0;
      }

      .contactMe {
        font-weight: 900;
        color: #ccc;
        padding: 1em 2.5em;
        letter-spacing: 1px;
        background-color: rgba(0, 0, 0, 0.2);
        border: 1px solid rgba(255, 255, 255, 0.15);
        -webkit-box-shadow: 0 0 0;
        -webkit-transition: all .2s ease-in-out;

        @media (max-width: 680px) {
          font-size: 2vh;
        }
        @media (max-width: 320px) {
          font-size: 1.5vh;
        }

        &.contactMe:hover {
          border: 2px solid $textColor_Primary;
        }
      }
    }
  }
}





